import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export const store = new Vuex.Store({
  state: {
    alertnum: 0,
    turn: [
      {
        turn: false,
        red: false,
      },
      {
        turn: false,
        red: false,
      },
      {
        turn: false,
        red: false,
      },
      {
        turn: false,
        red: false,
      },
    ],
    turnbool: false,
    redturn: 0,
    lock: false,
  },
  mutations: {
    // 直接改变 alertnum 的值
    changeAlertnum(state, num) {
      state.alertnum = num;
    },
    // 从数据改变 turn 的值
    changeTurn(state, alert, turn) {
      if (alert > 0 && alert <= 4) {
        state.turn[alert - 1].turn = true
        if (turn == 1) {
          state.turn[alert - 1].red = true
        }
      }
    },
    setTurnZero(state) {
      this.store.lock = true
      for (let i = 0; i < 4; i++) {
        if (state.turn[i].turn == true) {
          state.turn[i].turn = false
          state.turn[i].red = false
        }
      }
      this.store.lock = false
    },
    changeRedturn(state, i){
      state.redturn = i
    },
    changeTurnbool(state, bool){
      state.turnbool = bool
    },
    // lock(state) {
    //   state.lock = true
    // },
    // unlock(state) {
    //   state.lock = false
    // }
  },
  actions: {},
  modules: {},
});
