<template>
  <div>
    <button @click="playMusic" class="mu">
      <img :src="musicImg" class="musicImg" />
    </button>
    <audio ref="audio" :src="musicSource" loop class="audio"></audio>
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  name: "BGM",
  data() {
    return {
      isPlaying: false,
      musicImg: require("@/assets/images/button/music.png"),
      musicSource: require("@/assets/audio/NWGYU.wav"),
      nowstate: 0,
    };
  },
  computed: {
    ...mapState(["redturn"]),
  },
  watch: {
    redturn: {
      handler: function (newVal) {
        if (this.nowstate == newVal) {
          return;
        }
        console.log(newVal);
        if (newVal == 0) {
          this.nowstate = 0;
          this.musicSource = require("@/assets/audio/NWGYU.wav");
        } else if (newVal == 1) {
          this.nowstate = 1;
          this.musicSource = require("@/assets/audio/NWGYU.wav");
          this.playMusic()
        } else if (newVal == 2) {
          this.nowstate = 2;
          this.musicSource = require("@/assets/audio/NWGYU.wav");
          this.playMusic()
        } else if (newVal == 3) {
          this.nowstate = 3;
          this.musicSource = require("@/assets/audio/NWGYU.wav");
          this.playMusic()
        } else if (newVal == 4) {
          this.nowstate = 4;
          this.musicSource = require("@/assets/audio/NWGYU.wav");
          this.playMusic()
        }
      },
      deep: true,
    },
  },
  methods: {
    playMusic() {
      const audioElement = this.$refs.audio;
      audioElement.play();
      this.isPlaying = true;
    },
    togglePlay() {
      const audioElement = this.$refs.audio;
      if (this.isPlaying) {
        audioElement.pause();
      } else {
        audioElement.play();
      }
      this.isPlaying = !this.isPlaying;
    },
  },
};
</script>
<style lang="scss" scoped>
.musicImg {
  background-color: transparent;
  width: 30px;
  height: 30px;
}
.audio {
  width: 0%;
  height: 0%;
}
.mu {
  background-color: transparent;
  border: none;
  border-radius: 20%;
  &:hover {
    opacity: 0.8;
    background-color: #9999;
    cursor: pointer;
  }
  &:active {
    background-color: darken($color: #9999, $amount: 20%);
  }
}
</style>
