<template>
  <div class="button-bar">
    <div
      v-for="(buttonEl, index) in buttonEls"
      :key="index"
      class="button-container"
      :class="colortype[buttonEl.colortype].color"
    >
      <img :src="buttonEl.image" class="buttonImg" alt="button image" />
    </div>
  </div>
</template>

<script>
import {
  closedEyes,
  headTurn,
  phone,
  yawnEmoji,
} from "../../../assets/images/button";
import { mapState } from "vuex";
export default {
  name: "ButtonBar",
  data() {
    return {
      // lock: false,
      colortype: [
        {
          color: "grass_green_message",
        },
        {
          color: "yellow_message",
        },
        {
          color: "red_message",
        },
      ],
      buttonEls: [
        {
          type: "closed-eyes",
          image: closedEyes,
          colortype: 0,
        },
        {
          type: "yawn-emoji",
          image: yawnEmoji,
          colortype: 0,
        },
        {
          type: "phone-watch",
          image: phone,
          colortype: 0,
        },
        {
          type: "head-turn",
          image: headTurn,
          colortype: 0,
        },
      ],
    };
  },
  computed: {
    ...mapState(["alertnum","turnbool"]),
  },
  watch: {
    // 从 store 里面拿到 turn 的值
    alertnum: {
      handler: function (val) {
        for (let i = 0; i < 4; i++) {
          if (i == val-1) {
            this.buttonEls[i].colortype = 1
          } else {
            this.buttonEls[i].colortype = 0
          }
        }
      },
      deep: true,
    },
    turnbool: {
      handler: function (val) {
        if (val) {
          this.buttonEls[this.alertnum-1].colortype = 2
        }
      },
      deep: true,
    },
    // lock: {
    //   handler: function (val) {
    //     this.lock = val
    //   },
    //   deep: true,
    // },
  },
};
</script>

